<template>
    <a-row class="mt-40" :gutter="40">
      <a-col style="border: 1px solid transparent" :span="2"></a-col>
      <a-col :span="20">
        <a-row class="left-blue part-title can-not-select" style="width: 100%">
          <h1 style="display: inline"></h1>
          <div style="float: right">
            <a v-if="!userInfo.certificationVerified && userInfo.certificate.ID === 0" @click="certificateModalVisible = true" class="tag-desc"><a-icon style="color: gray;" theme="filled" type="down-circle" /></a>
            <a v-if="!userInfo.certificationVerified && userInfo.certificate.ID !== 0" @click="certificateModalVisible = true" class="tag-desc"><a-icon style="color: gray;" theme="filled" type="down-circle" /></a>
            <a v-if="userInfo.certificationVerified" @click="certificateModalVisible = true" class="tag-desc"><a-icon style="color: forestgreen;" theme="filled" type="down-circle" /></a>
          </div>
          <userVerify @changed="certificateChange" :editable="(userInfo.certificate.ID === 0) && (!userInfo.certificationVerified)" :passedInCertificateForm="certificateForm" :certificateModalVisible="certificateModalVisible" @closeUserVerifyModal="certificateModalVisible = false"></userVerify>
        </a-row>

        <div class="mt-20 sub-gray-line">
          <a-row>
            <h2 style="font-weight: bolder"></h2>
            <a-checkbox-group v-model="form.itemTypeList" :options="itemTypeOptions" />
            <a-input v-show="form.itemTypeList.includes('')" v-model="form.customItemType" style="width: 200px" size="small"></a-input>
          </a-row>

          <a-row class="mt-10">
            <h2 style="font-weight: bolder"></h2>
            <a-input size="small" style="width: 600px" v-model="form.title"></a-input>
          </a-row>

          <a-row class="mt-10">
            <h2 style="font-weight: bolder"></h2>
            <a-checkbox-group v-model="form.cooperationFormList" :options="cooperationFormOptions" />
          </a-row>

          <a-row class="mt-10">
            <h2 style="font-weight: bolder"></h2>
            <span class="blue">
              {{ itemTitle }}
            </span>
          </a-row>

          <a-row class="mt-10">
            <h2 style="font-weight: bolder"></h2>
            <a-col>
              <span style="margin-left: 28px"></span>
              <span v-for="item of cultureOptions" @click="switchFunctionOrClass(item)" :class="(form.functionOrClassList.includes(item) ? 'blue' : 'clickable-txt') + ' ml-10'" :key="item">
                {{ item }}
              </span>
            </a-col>

             <a-col class="mt-10">
               <span style="margin-left: 28px"></span>
              <span v-for="item of travelOptions" @click="switchFunctionOrClass(item)" :class="(form.functionOrClassList.includes(item) ? 'blue' : 'clickable-txt') + ' ml-10'" :key="item">
                {{ item }}
              </span>
            </a-col>

             <a-col class="mt-10">
               <span style="margin-left: 28px"></span>
              <span v-for="item of educationOptions" @click="switchFunctionOrClass(item)" :class="(form.functionOrClassList.includes(item) ? 'blue' : 'clickable-txt') + ' ml-10'" :key="item">
                {{ item }}
              </span>
            </a-col>

            <a-col class="mt-10">
              <span style="margin-left: 28px"></span>
              <span v-for="item of hospitalOptions" @click="switchFunctionOrClass(item)" :class="(form.functionOrClassList.includes(item) ? 'blue' : 'clickable-txt') + ' ml-10'" :key="item">
                {{ item }}
              </span>
            </a-col>

            <a-col class="mt-10">
              <span style="margin-left: 28px"></span>
              <span v-for="item of physicalOptions" @click="switchFunctionOrClass(item)" :class="(form.functionOrClassList.includes(item) ? 'blue' : 'clickable-txt') + ' ml-10'" :key="item">
                {{ item }}
              </span>
            </a-col>

            <a-col class="mt-10">
              <span style="margin-left: 28px"></span>
              <span v-for="item of businessOptions" @click="switchFunctionOrClass(item)" :class="(form.functionOrClassList.includes(item) ? 'blue' : 'clickable-txt') + ' ml-10'" :key="item">
                {{ item }}
              </span>
            </a-col>

            <a-col class="mt-10">
              
              <span v-for="item of headquarterOptions" @click="switchFunctionOrClass(item)" :class="(form.functionOrClassList.includes(item) ? 'blue' : 'clickable-txt') + ' ml-10'" :key="item">
                {{ item }}
              </span>
            </a-col>

            <a-col class="mt-10">
              
              <span v-for="item of industrialParkOptions" @click="switchFunctionOrClass(item)" :class="(form.functionOrClassList.includes(item) ? 'blue' : 'clickable-txt') + ' ml-10'" :key="item">
                {{ item }}
              </span>
            </a-col>
          </a-row>

          <a-row class="mt-10">
            <h2 style="font-weight: bolder"></h2>
            <a-col :span="12">
              <a-col>
                <a-input size="small" style="width: 50%" v-model="form.brand"></a-input>
              </a-col>
              <a-col class="mt-20">
                <a-input size="small" style="width: 50%" v-model="form.fullName"></a-input>
              </a-col>
            </a-col>
            <a-col :span="12">
              <a-col :span="3">
                logo
              </a-col>
              <a-col :span="6">
                <a-upload
                  :action="uploadPicURL"
                  name="file"
                  :file-list="form.brandLogoList"
                  :withCredentials="true"
                  @preview="handlePreview"
                  class="file-uploader"
                  list-type="picture-card"
                  @change="handleChange($event, ['form'],'brandLogoList')"
                >
                  <div v-if="form.brandLogoList.length < 1">
                    <a-icon :type="loading ? 'loading' : 'plus'" />
                  </div>
                </a-upload>
              </a-col>
            </a-col>
            <a-col :span="24">
              
              <a-textarea v-model="form.operationExperienceIntroduction" class="mt-10"></a-textarea>
            </a-col>
            <a-col class="mt-20 mb-20" :span="24">
              
              <a-upload-dragger
                name="file"
                :file-list="form.introductionFileList"
                :multiple="true"
                :withCredentials="true"
                :action="uploadFileURL"
                @change="handleChange($event, ['form'],'introductionFileList', 'file')"
              >
                <p class="ant-upload-drag-icon">
                  <a-icon type="inbox" />
                </p>
                <p class="ant-upload-text">
                  
                </p>
              </a-upload-dragger>
            </a-col>
          </a-row>
        </div>

        <div class="mt-20 sub-gray-line">

<!--          operation case begin-->

          <div>

            <a-radio @click.prevent="changeStatus('operationCase')" v-model="form.operationCase.status"></a-radio>

            <div v-show="form.operationCase.status" class="gray-board" style="padding: 10px 20px; border-bottom-left-radius: 0; border-bottom-right-radius: 0">
              <span v-if="form.operationCase.cases.length === 0" class="gray-font" style="font-size: 15px; font-weight: normal"></span>
              <span v-for="(_, index) of form.operationCase.cases" :key="'operationCase' + index" style="font-size: 15px; margin-right: 20px">
                <span @click="showCase(index)" :class="showCaseIndex === index ? 'blue' : 'clickable-txt'">{{ index + 1 }} </span>
                <span @click="deleteCase(index)" class="clickable-txt">[]</span>
              </span>
              <span class="clickable-txt" @click="addOperationCase" style="font-size: 15px; float: right"><a-icon type="plus"/></span>
            </div>

            <div v-show="form.operationCase.status" v-if="showCaseIndex !== null" style="border: solid 1px #e8e8e8; border-bottom-left-radius: 5px; border-bottom-right-radius: 5px; padding: 10px 20px">
              <h2 style="font-weight: bolder"></h2>

              <a-row>
                <span class="input-tag">: </span>
                <a-cascader style="width: 30%" v-model="form.operationCase.cases[showCaseIndex].location" :options="locationOptions" placeholder=""/>
              </a-row>

              <a-row class="mt-20">
                <span class="input-tag">: </span>
                <a-radio v-model="form.operationCase.cases[showCaseIndex].spaceType === 'aboveGround'" @click="form.operationCase.cases[showCaseIndex].spaceType = 'aboveGround'"> </a-radio>
                <a-radio v-model="form.operationCase.cases[showCaseIndex].spaceType === 'underGround'" @click="form.operationCase.cases[showCaseIndex].spaceType = 'underGround'"> </a-radio>
                <a-input v-model="form.operationCase.cases[showCaseIndex].space" type="number" style="width: 200px"></a-input>
                <span v-if="form.operationCase.cases[showCaseIndex].spaceType === 'aboveGround'">  m </span><span v-else>  </span>
              </a-row>

              <a-row class="mt-20">
                <span class="input-tag">: </span>
                <a-checkbox-group v-model="form.operationCase.cases[showCaseIndex].itemTypeList" :options="itemTypeOptions" />
                <a-input v-show="form.operationCase.cases[showCaseIndex].itemTypeList.includes('')" v-model="form.operationCase.cases[showCaseIndex].customItemType" style="width: 200px" size="small"></a-input>
              </a-row>

              <a-row class="mt-10">
                <h2 style="font-weight: bolder"></h2>
                <a-textarea v-model="form.operationCase.cases[showCaseIndex].introduction"></a-textarea>
                <span class="gray-font" style="font-weight: normal; font-size: 14px">
                  
                </span>
              </a-row>

              <a-row class="mt-10">
                <h2 style="font-weight: bolder"></h2>
                <a-col :span="12">
                  <a-upload
                    :action="uploadPicURL"
                    name="file"
                    :file-list="form.operationCase.cases[showCaseIndex].coverPicList"
                    :withCredentials="true"
                    @preview="handlePreview"
                    class="file-uploader"
                    list-type="picture-card"
                    @change="handleChange($event, ['form', 'operationCase', 'cases', showCaseIndex],'coverPicList')"
                  >
                    <div v-if="form.operationCase.cases[showCaseIndex].coverPicList.length < 1">
                      <a-icon :type="loading ? 'loading' : 'plus'" />
                    </div>
                  </a-upload>
                </a-col>
              </a-row>

              <a-row class="mt-10">
                <h2 style="font-weight: bolder">/</h2>
                <a-col :span="12">
                  <div style="display: inline-block" v-for="(pic, index) of form.operationCase.cases[showCaseIndex].otherPicList" :key="'otherPicList' + index">
                    <div @click="handlePreview(pic)" class="upload-add" style="display: flex; justify-content: center">
                      <img style="max-width: 100px; max-height: 100px; padding: 8px" :src="pic.thumbUrl" />
                    </div>
                    <div>
                      <a-input v-model="pic.description" placeholder="" style="width: 104px; margin-top: 10px"></a-input>
                    </div>
                  </div>
                  <a-upload
                    :action="uploadPicURL"
                    name="file"
                    :file-list="form.operationCase.cases[showCaseIndex].otherPicList"
                    :withCredentials="true"
                    @preview="handlePreview"
                    style="vertical-align: top"
                    class="file-uploader"
                    list-type="text"
                    @change="handleChange($event, ['form', 'operationCase', 'cases', showCaseIndex],'otherPicList')"
                  >
                    <div class="upload-add" v-if="form.operationCase.cases[showCaseIndex].otherPicList.length < 4">
                      <a-icon  class="upload-add-icon" :type="loading ? 'loading' : 'plus'" />
                    </div>
                  </a-upload>
                </a-col>
              </a-row>
            </div>

          </div>
<!--          operation case end-->

<!--           begin-->
          <div class="mt-10">
            <a-radio @click.prevent="changeStatus('plan')" v-model="form.plan.status"></a-radio>

            <div v-show="form.plan.status">
              <a-row class="mt-10">
                <h2 style="font-weight: bolder"></h2>
                <a-textarea v-model="form.plan.description"></a-textarea>
                <span class="gray-font" style="font-size: 14px; font-weight: normal">
                  
                </span>
              </a-row>

              <a-row class="mt-10">
                <h2 style="font-weight: bolder"></h2>
                <a-col :span="12">
                  <a-upload
                    :action="uploadPicURL"
                    name="file"
                    :file-list="form.plan.coverPicList"
                    :withCredentials="true"
                    @preview="handlePreview"
                    class="file-uploader"
                    list-type="picture-card"
                    @change="handleChange($event, ['form', 'plan'],'coverPicList')"
                  >
                    <div v-if="form.plan.coverPicList.length < 1">
                      <a-icon :type="loading ? 'loading' : 'plus'" />
                    </div>
                  </a-upload>
                </a-col>
              </a-row>

              <a-row class="mt-10">
                <h2 style="font-weight: bolder">/</h2>
                <a-col :span="12">
                  <div style="display: inline-block" v-for="(pic, index) of form.plan.otherPicList" :key="'otherPicList' + index">
                    <div @click="handlePreview(pic)" class="upload-add" style="display: flex; justify-content: center">
                      <img style="max-width: 100px; max-height: 100px; padding: 8px" :src="pic.thumbUrl" />
                    </div>
                    <div>
                      <a-input v-model="pic.description" placeholder="" style="width: 104px; margin-top: 10px"></a-input>
                    </div>
                  </div>
                  <a-upload
                    :action="uploadPicURL"
                    name="file"
                    :file-list="form.plan.otherPicList"
                    :withCredentials="true"
                    @preview="handlePreview"
                    style="vertical-align: top"
                    class="file-uploader"
                    list-type="text"
                    @change="handleChange($event, ['form', 'plan'],'otherPicList')"
                  >
                    <div class="upload-add" v-if="form.plan.otherPicList.length < 4">
                      <a-icon  class="upload-add-icon" :type="loading ? 'loading' : 'plus'" />
                    </div>
                  </a-upload>
                </a-col>
              </a-row>
            </div>
          </div>

<!--           end-->

        </div>
      </a-col>

      <!--      modal begin-->
      <a-modal width="80%" :visible="previewVisible" :footer="null" @cancel="previewVisible = false">
        <img style="width: 100%" :src="previewImage" />
      </a-modal>
      <!--      modal end-->
    </a-row>
</template>

<script>
import {mapActions, mapGetters} from 'vuex'
import userVerify from '../../components/userVerify'
import utils from '@/utils/utils'
import { HTTP } from '@/utils/constants'
import options from '@/utils/cities'

const industrialParkOptions = [
  '',
  '',
  '',
  '',
  '',
  ''
]

const headquarterOptions = [
  '',
  '',
  '',
  '',
  '+',
  '',
  ''
]

const cooperationFormOptions = [
  '',
  '',
  'IP ',
  '',
  ' + '
]

const businessOptions = [
  '',
  '',
  '',
  '',
  '',
  ''
]

const physicalOptions = [
  '',
  '',
  '',
  '',
  '',
  '',
  ''
]

const travelOptions = [
  '',
  '',
  '',
  '',
  '',
  '',
  '',
  ''
]

const hospitalOptions = [
  '',
  '',
  '',
  '',
  '',
  '',
  ''
]

const educationOptions = [
  '',
  'K9 ',
  'K12 ',
  '',
  '',
  '',
  ''
]

const cultureOptions = [
  '',
  '',
  '',
  '',
  '',
  ''
]

const itemTypeOptions = [
  '',
  '',
  '',
  '',
  '',
  '',
  '',
  '',
  ''
]

export default {
  name: 'publishIndustryResource',
  components: {
    userVerify
  },
  computed: {
    ...mapGetters(['userInfo']),
    'itemTitle' () {
      let res = ''
      if (this.form.itemTypeList.length) {
        res += ''
        for (let itemType of this.form.itemTypeList) {
          if (res !== '') {
            res += '/'
          }
          if (itemType !== '') res += itemType
        }
        if (this.form.itemTypeList.includes('')) res += this.form.customItemType
        res += ' '
      }

      if (this.form.title) {
        res += this.form.title + ' '
      }

      if (this.form.cooperationFormList.length) {
        for (let cooperationForm of this.form.cooperationFormList) {
          if (res && res[res.length - 1] !== ' ' && !cooperationForm.includes('+')) {
            res += '/'
          }
          res += cooperationForm
        }
      }

      if (!res) return ''
      return res
    }
  },
  data () {
    return {
      form: {
        operationCase: {
          status: false,
          cases: []
        },
        plan: {
          status: false,
          introduction: '',
          coverPicList: [],
          otherPicList: []
        },
        itemTypeList: [],
        cooperationFormList: [],
        brandLogoList: [],
        introductionFileList: [],
        functionOrClassList: [],
        customItemType: '',
        fullName: '',
        title: '',
        operationExperienceIntroduction: '',
        brand: ''
      },
      previewVisible: false,
      loading: false,
      uploadPicURL: '',
      uploadFileURL: '',
      previewImage: '',
      locationOptions: options,
      industrialParkOptions: industrialParkOptions,
      headquarterOptions: headquarterOptions,
      itemTypeOptions: itemTypeOptions,
      businessOptions: businessOptions,
      physicalOptions: physicalOptions,
      educationOptions: educationOptions,
      cultureOptions: cultureOptions,
      hospitalOptions: hospitalOptions,
      travelOptions: travelOptions,
      cooperationFormOptions: cooperationFormOptions,
      certificateForm: {},
      certificateModalVisible: false,
      showCaseIndex: null
    }
  },
  mounted () {
    this.uploadPicURL = process.env.API_ROOT + '/system/pics/'
    this.uploadFileURL = process.env.API_ROOT + '/system/files/'
  },
  methods: {
    ...mapActions(['getUserInfo']),
    async certificateChange () {
      await this.getUserInfo()
      this.certificateForm = this.userInfo.certificate
    },
    async handlePreview (file) {
      if (!file.uuid && !file.preview && file.response.data.status === 'temp') {
        file.preview = await utils.getBase64(file.originFileObj)
      }
      this.previewImage = (process.env.API_ROOT + '/system/pics/temp/' + file.uuid + '/') || file.preview
      this.previewVisible = true
    },
    deleteCase (index) {
      this.form.operationCase.cases.splice(index, 1)

      if (index <= this.showCaseIndex) {
        this.showCaseIndex -= 1
      }

      if (this.showCaseIndex < 0) {
        if (this.form.operationCase.cases.length) {
          this.showCaseIndex = 0
        } else {
          this.showCaseIndex = null
        }
      }
    },
    showCase (index) {
      this.showCaseIndex = index
    },
    addOperationCase () {
      if (this.form.operationCase.cases.length > 9) {
        this.$info('')
        return
      }

      // mark1
      this.form.operationCase.cases.push({
        location: [],
        spaceType: 'aboveGround',
        space: 0,
        itemTypeList: [],
        coverPicList: [],
        otherPicList: [],
        customItemType: '',
        introduction: ''
      })

      this.showCaseIndex = this.form.operationCase.cases.length - 1
    },
    changeStatus (index) {
      this.form[index].status = !this.form[index].status
    },
    async handleChange (info, forms, list, type = 'pic') {
      let form = this

      for (let temp of forms) {
        form = form[temp]
      }

      if (info.fileList.length === 0) {
        form[list] = []
        return
      }

      if (type === 'pic' && !info.fileList[info.fileList.length - 1].thumbUrl) {
        info.fileList[info.fileList.length - 1].thumbUrl = await utils.getBase64(info.file.originFileObj)
      }

      this.loading = true

      if (!info.file.response) {
        info.fileList[info.fileList.length - 1].status = 'uploading'
      } else if (info.file.response.code !== HTTP.SUCCESS) {
        info.fileList[info.fileList.length - 1].status = 'error'
        this.loading = false
      } else {
        // Get this url from response in real world.
        this.loading = false
        info.fileList[info.fileList.length - 1].status = 'done'
        info.fileList[info.fileList.length - 1].uuid = info.file.response.data.uuid
      }

      form[list] = info.fileList
    },
    switchFunctionOrClass (item) {
      if (this.form.functionOrClassList.includes(item)) {
        this.form.functionOrClassList.remove(item)
        return
      }
      this.form.functionOrClassList.push(item)
    }
  }
}
</script>

<style scoped>

</style>



